import React, { useState } from 'react';
import {Header} from '../Constants/header';
import {Footer} from '../Constants/footer';
import {UserInformationContext} from "../Context/index";
import {LoginPage} from "../pages/login";
import {Dashboard} from "../pages/dashboard";
import {
  BrowserRouter as Router,
  Route,
} from "react-router-dom";

import "./styles.css";

function App() {
  let [userInfo, updateUserInfo] = useState(null);

  function handleUpdateUserInfo(response: any) {
    updateUserInfo((prevState) => {
      return (response);
    })
  }

  function getComponent() {
    if (userInfo) {
      return (
        <Router>
          <Route>
            <Dashboard/>
          </Route>
        </Router>
      )
    } else {
      return <LoginPage/>
    }
  }

  return (
    <div> 
      <Header/>
      <UserInformationContext.Provider value={{userInfo, updateUserInfo: handleUpdateUserInfo}}>
        <div className="middle-section">
          {getComponent()}
        </div>
      </UserInformationContext.Provider>
      <Footer/>

    </div>
  );
}

export default App;
