interface validationTemplate {
    status: boolean,
    message: string | null,
}

interface UserCredentials {
    userId: string | null,
    password: string | null;
}
export enum PageTypes {
    WELCOME = "WELCOME",
    SECTION_START = "SECTION_START",
    QUIZ = "QUIZ",
    INFO_VIDEO = "INFO_VIDEO",
    QUIZ_RESULTS = "QUIZ_RESULTS",
    FINISH = "FINISH"

}
export enum Status {
    INCOMPLETE = "INCOMPLETE",
    UPCOMING = "UPCOMING",
    EXPIRED = "EXPIRED",
    COMPLETE = "COMPLETE",
}
export function validateLogin(userCredentials: UserCredentials) {
    const validation: validationTemplate = {
        status: true,
        message: null,
    }

    if (!userCredentials.userId) {
        validation.status = false;
        validation.message = "Please enter user id"

        return validation;
    }
    if (!userCredentials.password) {
        validation.status = false;
        validation.message = "Please enter password"

        return validation;
    }

    return validation;
}

