import React, {useContext} from 'react';
import {UserInformationContext} from "../../Context/index";
import {MetaTrainingDetails} from "./metaTrainingDetails";
import {Status} from "../utils";

export function TrainingList() {
    const getUserInformationContext: any = useContext(UserInformationContext);
    
    function getTrainings(listOfTrainings: any, trainingTypes: string[]) {
        return listOfTrainings.map((trainingDetails: any, index: number) => {
            return getReturnElement(index, trainingDetails.trainingName, trainingDetails.status, trainingDetails.lastDate, trainingDetails.priority, trainingTypes);  
        })
    }

    function getReturnElement(index: number, trainingName: string, status: Status, lastDate: string, priorityRank: number, checkFor: string[])  {
        if (checkFor.includes(status)) {
            return <MetaTrainingDetails
                    key={index}
                    trainingIndex={index}
                    trainingName={trainingName}
                    status={status}
                    lastDate={lastDate}
                    priorityRank={priorityRank}
                />    
        } else {
            return <div key={index}/>
        }
    }
   
    return (
        <div className="container-height container-fluid justify-content-center main-container">
            <div className="row mt-4">    
                <div className="col-md-8 offset-md-2">
                    <h3>Current Trainings</h3>
                    {
                        getTrainings(getUserInformationContext.userInfo.trainings, [Status.INCOMPLETE, Status.COMPLETE])

                    }
                </div>
            </div>
            <div className="row mt-4">
                <div className="col-md-8 offset-md-2">
                    <h3>Upcoming Trainings</h3>
                    {
                        getTrainings(getUserInformationContext.userInfo.trainings, [Status.UPCOMING])
                    }
                </div>
            </div> 
            <div className="row mt-4">
                <div className="col-md-8 offset-md-2">
                    <h3>Expired Trainings</h3>
                    {
                        getTrainings(getUserInformationContext.userInfo.trainings, [Status.EXPIRED])
                    }
                </div>
            </div>   
        </div>
    );
}
