import React, {useState, useEffect} from "react";
import {PageTypes} from "../../utils";
import {getIntroductionPages, getQuize, getInfoVideo, getQuizResult, getFinishPage} from "../utils";
import "./styles.css";

interface IProps {
    pageDetails: any;
    currentPageIndex: number;
    handleClickNext: any;
    reattemptQuiz: any;
}

let quizDetails: any = null;

export function Content(props: IProps) {
    const [userAnswers, setAnswers] = useState<any | any>({});
    const [totalCorrectAns, setCorrectAnswers] = useState<any | any>(0);

    function handleAnswerChange(key: string, questionId: number) {
        let currentAnswers = userAnswers;
        if (currentAnswers.hasOwnProperty(questionId)) {
            if (currentAnswers[questionId].includes(key)) {
                currentAnswers[questionId].splice(currentAnswers[questionId].indexOf(key), 1)
                if (currentAnswers[questionId].length === 0) {
                    delete currentAnswers[questionId];
                }
            } else {
                currentAnswers[questionId].push(key);
            }
        } else {
            currentAnswers[questionId] = [key];
        }
        setAnswers((prevState: any) => {
            return {
                ...currentAnswers,
            }
        })
    }
    function getContentBody(pageDetails: any) {
        if (pageDetails.type === PageTypes.WELCOME || pageDetails.type === PageTypes.SECTION_START) return getIntroductionPages(pageDetails.mdText, pageDetails.mascotPlacement);
        if (pageDetails.type === PageTypes.QUIZ) return getQuize(pageDetails.questions, userAnswers, handleAnswerChange);
        if (pageDetails.type === PageTypes.INFO_VIDEO) return getInfoVideo(pageDetails.videoUrl, pageDetails.mdText);
        if (pageDetails.type === PageTypes.QUIZ_RESULTS) return getQuizResult(totalCorrectAns, quizDetails, reattemptQuiz, props.pageDetails.quizPageId);
        if (pageDetails.type === PageTypes.FINISH) return getFinishPage();
    }
    function handleNext(pageDetails: any, currentValues: any, pageType: string, currentPageIndex: number) {
        if (pageType === PageTypes.QUIZ) {
            let correctAnswers = 0;
            pageDetails.questions.forEach((obj: any) => {
                if(currentValues.hasOwnProperty(obj.id)
                    && currentValues[obj.id].length === obj.answers.length
                    && JSON.stringify(currentValues[obj.id]) === JSON.stringify(obj.answers)
                ) {
                    correctAnswers++;
                } 
            })
            quizDetails = pageDetails;
            setCorrectAnswers(correctAnswers)
        }
        props.handleClickNext(currentPageIndex)
    }
    function reattemptQuiz(quizPageId: number) {
        setAnswers({});
        props.reattemptQuiz(quizPageId);
    }
    return (
        <div>
            {getContentBody(props.pageDetails)}

            {props.pageDetails.type !== PageTypes.FINISH && <button className="next-btn"
                onClick={() =>  handleNext(props.pageDetails, userAnswers, props.pageDetails.type, props.currentPageIndex)}
            >
                NEXT
            </button>}
        </div>
    )
}