import React from "react";
import {PageTypes} from "../../utils";
import "./styles.css";

interface IProps {
    pageIndex: number;
    handleClick: any;
    pageType: PageTypes;
    currentPageIndex: number;
    navigablePageIndexes: number[];
}

export function SideNav(props: IProps) {
    function getPageName(pageType: string) {
        switch(pageType) {
            case PageTypes.WELCOME: return "WELCOME";
            case PageTypes.SECTION_START: return "INTRODUCTION";
            case PageTypes.QUIZ: return "QUIZ";
            case PageTypes.INFO_VIDEO: return "INFO VIDEO";
            case PageTypes.QUIZ_RESULTS: return "RESULTS";
            case PageTypes.FINISH: return "FINISH";
        }
    }
    return (
        <div className={`page mt-2 mb-2 ${props.pageIndex === props.currentPageIndex ? "page-selected" : ""}`}
            onClick={() => props.handleClick(props.pageIndex, props.navigablePageIndexes)}
        >
            <b>{getPageName(props.pageType)}</b>
        </div>
    )
} 