import React from 'react';
import {Status} from "../utils";
import "./styles.css";
import {Link } from 'react-router-dom';

interface IProps {
    trainingName: string;
    status: Status;
    lastDate: string;
    priorityRank: number;
    trainingIndex: number;
}

export function MetaTrainingDetails(props: IProps) {
    const styles: any = {
        [Status.INCOMPLETE]: {
            border: "red",
            background: "#fc9fa2",
            cursor: "pointer",
        }, 
        [Status.UPCOMING]: {
            border: "gray",
            background: "#EEEDEC",
            cursor: "not-allowed",
        },
        [Status.EXPIRED]: {
            border: "#713A02 ",
            background: "#FEC790",
            cursor: "not-allowed",
        },
        [Status.COMPLETE]: {
            border: "#38A103",
            background: "#99F56C",
            cursor: "not-allowed",
        }
    }
    function isRedirectable(status: string) {
        return !(status === Status.EXPIRED || status === Status.UPCOMING || status ===  Status.COMPLETE)
    }
    return (
        <Link to={{pathname: `${isRedirectable(props.status) ? "/training" : ""}`, state: {trainingIndex: props.trainingIndex}}}
            style={{ textDecoration: "none", color: "black"}}
        >
            <div className="box mt-2 mb-2"
                style={{
                    borderLeft: `solid 4px ${styles[props.status].border}`,
                    backgroundColor: styles[props.status].background,
                    cursor: styles[props.status].cursor,
                }}
            >
                <div>
                    <b className="floar-left">{props.trainingName}</b>
                    <span className="float-right meta-font">{props.status.toUpperCase()}</span>
                </div>
                
                <div className="meta-font mt-2">
                    Last Date: {props.lastDate} | Priority Rank: {props.priorityRank}
                </div>
            </div>
        </Link>
    );
}