import React, {useState, useContext} from 'react';
import {TextField} from "../utilities/inputFunctions";
import logInHero from "../images/Kids Reading.svg";
import {validateLogin, testLoginResponseJson} from "./utils";
import {makePostRequest} from "../ApiCalls/index";
import {UserInformationContext} from "../Context/index";
import "./styles.css";

export function LoginPage(props: any) {
    let [userCredentials, handleChangeUserCredential] = useState({userId: null, password: null});
    const getUserInformationContext: any = useContext(UserInformationContext)

    function onChangeHandler(fieldName: string, value: string | number) {
        handleChangeUserCredential((prevState) => {
            return ({
                ...prevState,
                [fieldName]: value
            });
        })
    }

    function handleSubmit() {
        const validation = validateLogin(userCredentials);
        if (validation.status) {
            // let validateLogin = makePostRequest('url', {userId: userCredentials.userId, password: userCredentials.password});
            // validateLogin.then((response) => {
            //     getUserInformationContext.updateUserInfo(response.data);
            // })
            // validateLogin.catch((error) => {
            //     error will be added here
            // })
            
            // Using value for testing purpose
            getUserInformationContext.updateUserInfo(testLoginResponseJson);
        }
    }
    return (
        <div className="container">
            <div className="row">
                <div className="col-sm-6 text-center">
                    <img src={logInHero} alt="Login Hero" className="width-100"/>
                </div>
                <div className="col-sm-6 pl-4 pr-4 mt-4">
                    <TextField
                        type= "text"
                        value = {userCredentials.userId}
                        onChange = {onChangeHandler}
                        fieldName = 'userId'
                        label = "Please Enter Your User Id"
                        placeholder={"ApoorvKumar"}
                    />

                    <TextField
                        type = "password"
                        value = {userCredentials.password}
                        onChange = {onChangeHandler}
                        fieldName = "password"
                        label = "Please Enter Your Password"
                    />

                    <button className="submit-login mt-4"
                        onClick={handleSubmit}
                    >
                        Submit
                    </button>
                </div>
            </div>
        </div>
    )
}