import React from "react";
import owl from "../../images/owl.png";
import "./styles.css"

function isAnswerChecked(userAnswers: any, questionId: number, key: string) {
    return userAnswers.hasOwnProperty(questionId) && userAnswers[questionId].includes(key)
}

export function getIntroductionPages(text: string, mascotPlacement: any) {
    return (
        <div className="text-center welcome">
            <img src={owl} alt="mascot"/>
            <b className="welcome-text">{text}</b>
        </div>
    )
}
export function getFinishPage() {
    return (
        <div className="mt-4 pt-4 suggestion-box text-center">
                    <div>
                        <div><h4>Thank you for completing the training!</h4></div>
                        <div>
                            <textarea className="mt-2">
                                Do you have any suggestions for us ? Please write us here
                            </textarea>
                        </div>
                        <div>
                            <button className="mt-2 custom-btn">Submit Suggestion</button>
                        </div>
                    </div>
        </div>
    )
}
export function getQuizResult(totalCorrectAnswered: any, quizDetails: any, reattemptQuiz: any, quizPageId: number) {
    const passPercent = quizDetails.passPercent;
    const totalQuestions = quizDetails.questions.length;
    const userPercentage = (passPercent && totalQuestions) ? (100/totalQuestions)*totalCorrectAnswered : 0
    return (
        <div className="result text-center mt-4">
            <div style={{width: "60%", margin: "auto"}}>
                {(passPercent && userPercentage < passPercent) && <div className="row">
                    <div className="col-sm-3">
                        <img src={owl} alt="mascot" style={{width: "100%"}}/>
                    </div>
                    <div className="col-sm-9">
                        <div className="reattempt-text">
                            You scored {userPercentage}% in the quiz. You need {passPercent}% to complete. Please reattempt the quiz.
                            Check the answers below.
                        </div>
                    </div>
                </div>}
                <h5>Your Obtained Percentage</h5>
                <h2 style={{color: (passPercent && userPercentage < passPercent) ? "red" : "green"}}>{userPercentage}%</h2>
                <div className="progress">
                    <div className="progress-bar progress-bar-striped" role="progressbar" style={{width: `${userPercentage}%`}}/>
                </div>
            </div>
            {(passPercent && userPercentage < passPercent) && <div>
                <button className="mt-2 custom-btn"
                    onClick={() => reattemptQuiz(quizPageId)}
                >Re-attempt</button>
                <div className="row">
                    <div className="col-sm-12 text-left">
                        {showCorrectAnswers(quizDetails.questions)}
                    </div>
                </div>
            </div>}

            {(passPercent && userPercentage >= passPercent) && <div>
                <button className="mt-4 custom-btn">Submit Answers</button>
            </div>}
        </div>
    )
}
function showCorrectAnswers(questions: any) {
    let correctAnswers: any = {};
    questions.forEach((question: any) => {
        correctAnswers[question.id] = question.answers;
    });
    return getQuize(questions, correctAnswers)
}
export function getQuize(questions: any, userAnswers: object, handleChange?: any,) {
    return (
        <div className="question-container">
            {
                questions.map((questionObject: any, index: number) => {
                    return (
                        <div className="mt-4 mb-4"
                            key={index}
                        >
                            <span className="qsNo mb-4">Question {index+1}</span>
                            <div className="question mt-2"> {questionObject.mdText}</div>
                            <div className="options mt-2">
                            <div className="wdyt">{questionObject.question}</div>
                            {questionObject.options.map((option: any, ind: number) => {
                                return (
                                    <button value={option.key}
                                        key={ind}
                                        className={`option-btn ${isAnswerChecked(userAnswers, questionObject.id, option.key) ? "checked" : "unchecked"}`}
                                        onClick={handleChange ? () => handleChange(option.key, questionObject.id) : undefined}
                                    > 
                                        <i className={`ml-1 mr-2 fa ${isAnswerChecked(userAnswers, questionObject.id, option.key) ? "fa-check-square-o" : "fa-square-o"}`}></i>
                                        {option.optionText}
                                    </button>
                                )
                            })}
                            </div>
                        </div>
                    )
                })
            }
        </div>
    )
}

export function getInfoVideo(videoUrl: string, text: string) {
    return (
        <div className="mt-4">
            <div className="mt-1 mb-2 text-center">
                <b>Please watch the video</b>
            </div>
            
            <div className="holder text-center">
                <iframe width="600px" height= "300px" className="vidframe" src={videoUrl} allow={"accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"}></iframe>
                <div className="topbar" onClick={undefined}></div>
                <div className="lowerbar"  onClick={undefined}></div>
            </div>
            <div className="iframe-text text-left">{text}</div>
        </div>
    )
}